<template>
  <div>
    <a-card title="ozon订单管理">
      <a-row style="display: flex;">

        <a-col style="width: 300px; margin-bottom: 12px; margin-right: 20px;">
          <a-input style="width: 200px; margin-right: 10px" v-model="searchForm.keyWord" placeholder="关键字查询.." allowClear @pressEnter="search" />
          <a-button type="primary" icon="search" @click="search">查询</a-button>
        </a-col>

        <a-col style="max-width: 620px; margin-bottom: 12px; margin-right: 10px;">
          <a-select v-model="searchForm.system_status" placeholder="请选择" style="width: 200px; margin-right: 10px"> 
            <a-select-option v-for="option in options_status" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>

          <a-select v-model="searchForm.order_way" placeholder="请选择" style="width: 200px; margin-right: 10px;"> 
            <a-select-option v-for="option in options_order_way" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>

          <a-select v-model="searchForm.options_match" placeholder="请选择" style="width: 200px;"> 
            <a-select-option v-for="option in options_match" :value="option" :key="option">{{ option }}</a-select-option> 
          </a-select>
        </a-col>

        <a-col>
          <a-range-picker
            v-model="dateRange"
            format="YYYY-MM-DD HH:mm:ss"
            show-time
            style="width: 360px; margin-right: 20px"
            @change="search"
          />
        </a-col>

        <a-col>
          <span> 一页显示数量： </span> <a-input style="width:80px" v-model="default_number" /> <span>条</span>
        </a-col>
    
      </a-row>

      <!-- 列设置菜单 -->
      <a-row style="display: flex;">

        <a-col style="margin-right: 10px;">
          <a-dropdown>
            <a-menu slot="overlay">
              <draggable v-model="dynamicColumns" handle=".drag-handle">
                <a-menu-item v-for="(col) in dynamicColumns" :key="col.key">
                  <a-checkbox v-model="col.visible">
                    <a-icon type="menu" class="drag-handle" style="margin-right: 8px;" />
                    {{ col.title }}
                  </a-checkbox>
                </a-menu-item>
              </draggable>
            </a-menu>
            <a-button>
              设置列 <a-icon type="down" />
            </a-button>
          </a-dropdown>
        </a-col>

        <a-col style="margin-right: 10px;">
          <a-button type="primary" @click="handleSelectedRows" style="margin-bottom: 12px;">
            单产品订单一键审核
          </a-button>       
        </a-col>
        
        <a-col style="margin-right: 10px;">
          <a-button type="primary" @click="handleSelectedRows" style="margin-bottom: 12px;">
            批量通过审核
          </a-button>       
        </a-col>

        <a-col style="margin-right: 10px;">
          <a-button type="primary" @click="exportExcel" style="margin-bottom: 12px;">
            导出全部订单
          </a-button>       
        </a-col>
        
      </a-row>

      <a-row style="margin-top: 12px;">
        <a-table
          size="small"
          :columns="dynamicColumns.filter(col => col.visible)"
          :dataSource="items"
          rowKey="id"
          :loading="loading"
          :pagination="pagination"
          @change="tableChange"
          :rowSelection="rowSelection"
        >

          <div slot="is_match" slot-scope="text, record">
            <span :style="{
              color: record.products.every(item => item.system_products.length > 0) ?'green':'red'
            }"> {{ record.products.every(item => item.system_products.length > 0) ?"已匹配":"未匹配" }} </span>
          </div>

          <div slot="is_active" slot-scope="value">
            <a-tag :color="value ? 'green' : 'red'">{{ value ? '激活' : '冻结' }}</a-tag>
          </div>

          <div slot="products" slot-scope="text, record">
            <div v-for="product, index in record.products" :key="index + 100">
              <span> {{ index + 1 }}. </span>
              <span> offer_id: </span> <span style="color: red;"> <a :href="`https://ozon.ru/product/${product.sku}`" target="_blank"> {{ product.offer_id }} </a> </span> 
              <span> 价格: </span> <span style="color: green;">{{ product.price }}</span> 
              <span> 数量: </span> <span style="color: skyblue;">{{ product.quantity }}</span>
              <hr />
            </div>
          </div>
          <div slot="action" slot-scope="value, item">
            <a-button-group>
              <a-button icon="edit" size="small" @click="openFormModal(item)">查看</a-button>
              <a-button icon="edit" size="small" @click="openApprovalFormModal(item)">审核</a-button>
              <a-popconfirm
                v-if="item.system_status == '已冻结'"
                title="确定解冻该订单吗"
                @confirm="unfreeze(item.id)"
              >
                <a-button type="danger" icon="delete" size="small">解冻</a-button>
              </a-popconfirm>
              <a-popconfirm
                v-else
                title="确定冻结该订单吗"
                @confirm="freeze(item.id)"
              >
                <a-button type="danger" icon="delete" size="small">冻结</a-button>
              </a-popconfirm>
            </a-button-group>
          </div>
        </a-table>
      </a-row>
    </a-card>
    <form-modal v-model="visible" :form="targetItem" @update="update" />
    <approval-modal v-model="approval_visible" :visible="approval_visible" :form="targetItem" @update="update" />
  </div>
</template>

<script>
import moment from 'moment';
import { getOzonOrderData, freezeOrder, unfreezeOrder,getDataToExcel } from '@/api/operation';
import draggable from 'vuedraggable'; // 导入 vuedraggable

import ozonStatus from '@/utils/constant';

export default {
  name: 'OzonOrder',
  components: {
    FormModal: () => import('./FormModal.vue'),
    ApprovalModal: () => import('./ApprovalFormModal.vue'),
    draggable,
  },
  data() {
    return {
      columns: [
        { title: '是否匹配商品', dataIndex: 'products', key: 'products', scopedSlots: { customRender: 'is_match' } },

        { title: '订单ID', dataIndex: 'order_id', key: 'order_id' },
        { title: '货件号', dataIndex: 'posting_number', key: 'posting_number' },
        { title: '国际运单号', dataIndex: 'tracking_number', key: 'tracking_number' },
        { 
          title: '货运状态', 
          dataIndex: 'posting_status', 
          key: 'posting_status',
          customRender: (value) => ozonStatus[value],
        },
        { 
          title: '物流状态', 
          dataIndex: 'logistics_status', 
          key: 'logistics_status',
          customRender: (value) => ozonStatus[value],
        },
        { title: '订单总价', dataIndex: 'total_price', key: 'total_price' },
        
        {
          title: '重量',
          dataIndex: 'weight',
          key: 'weight',
        },

        { title: '系统状态', dataIndex: 'system_status', key: 'system_status' },
        {
          title: '开始处理时间',
          dataIndex: 'in_process_at',
          key: 'in_process_at',
          customRender: (value) => moment(value).format('YYYY-MM-DD HH:mm:ss'),
        },
        {
          title: '必须收取时间',
          dataIndex: 'shipment_date',
          key: 'shipment_date',
          customRender: (value) => moment(value).format('YYYY-MM-DD HH:mm:ss'),
        },

        {
          title: '是否系统审核',
          dataIndex: 'audit_in_system',
          key: 'audit_in_system',
          customRender: (value) => value?"是":"否",
        },
        {
          title: '审核时间',
          dataIndex: 'approval_time',
          key: 'approval_time',
        },

        {
          title: '出库时间',
          dataIndex: 'dispatch_time',
          key: 'dispatch_time',
        },
        
        { title: '产品', dataIndex: 'products', key: 'products', scopedSlots: { customRender: 'products' } },
        {
          title: '关联店铺',
          dataIndex: 'shop',
          key: 'shop',
          customRender: (value) => value.name,
        },
        {
          title: '负责人',
          dataIndex: 'owner',
          key: 'owner',
          customRender: (value) => (value ? value.name : '店铺未绑定'),
        },
        { title: '操作', dataIndex: 'action', key: 'action', scopedSlots: { customRender: 'action' } },
      ],

      dynamicColumns: [], // 动态列数组
      
      searchForm: { start: 0, limit: 10, keyWord: '', system_status: '已审核待备货', order_way: '逆序', options_match: '全部'  },
      pagination: { current: 1, total: 0, pageSize: 10 },
      default_number: 10,

      loading: false,
      items: [],
      visible: false,
      approval_visible: false,
      targetItem: {},
      form: {},
      importLoading: false,
      options_status: [
        "全部","已创建待审核","等待国际运单号同步","已审核待备货","已备货待出库","已出库待发货",
        "已发货待签收","仲裁中","已完成","未知状态","已取消","已冻结"
      ],
      options_order_way:[
        "顺序","逆序"
      ],
      options_match:[
        "全部","未匹配","已匹配"
      ],
      dateRange: [
        moment().startOf("day").subtract(1, "month"),
        moment().endOf("day"),
      ],
      selectedRowKeys: [], // 存储选中行的key
      selectedRows: [], // 存储选中行的完整数据
    };
  },
  computed: {
    rowSelection() {
      return {
        selectedRowKeys: this.selectedRowKeys, // 当前选中的key
        onChange: this.onSelectChange, // 监听选择变化
      };
    },
  },
  watch:{
    'searchForm.system_status'(newValue,oldValue){
      this.loading = true;
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    'searchForm.order_way'(newValue,oldValue){
      this.loading = true;
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    'searchForm.options_match'(newValue,oldValue){
      this.loading = true;
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    default_number(newValue,oldValue){

      const num = Number(newValue);
      console.log(num)

      if (Number.isInteger(num) & num != 0){
        this.searchForm.limit = newValue
        this.pagination.current = 1
        this.searchForm.start = 0
        this.pagination.pageSize = newValue
        this.list()
      }
    }
  },
  methods: {
    initialize() {
      this.list();
    },
    list() {
      this.loading = true;
      getOzonOrderData({...this.searchForm,"keyWord":this.searchForm.keyWord in ozonStatus?ozonStatus[this.searchForm.keyWord]:this.searchForm.keyWord,"dateRange":JSON.stringify(this.dateRange.map(date => date.format('YYYY-MM-DD HH:mm:ss')))})
        .then((data) => {
          this.pagination.total = data.data.count;
          this.items = data.data.data;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    update() {
      this.visible = false;
      this.approval_visible = false;
      this.list();
    },
    search() {
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    openFormModal(item) {
      this.targetItem = { ...item };
      this.visible = true;
    },
    openApprovalFormModal(item) {
      this.targetItem = { ...item };
      this.approval_visible = true;
    },
    freeze(id) {
      freezeOrder({ order_id: id }).then((res) => {
        this.$message.success(res.msg);
        this.list();
      });
    },
    unfreeze(id) {
      unfreezeOrder({ order_id: id }).then((res) => {
        this.$message.success(res.msg);
        this.list();
      });
    },
    tableChange(pagination) {
      this.searchForm.start = (pagination.current - 1) * this.pagination.pageSize;
      this.pagination.current = pagination.current;
      this.list();
    },
    // 处理勾选变化事件
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys; // 更新选中的key
      this.selectedRows = selectedRows; // 更新选中的行数据
    },
    handleSelectedRows() {
      // 获取所有选中行的ID
      const selectedIds = this.selectedRows.map(row => row.id);
      console.log('选中的ID:', selectedIds);
      this.$message.error("系统测试阶段，暂不支持使用");
    },
    exportExcel(){
      this.$message.success("正在导出中..");
      getDataToExcel({...this.searchForm,"keyWord":this.searchForm.keyWord in ozonStatus?ozonStatus[this.searchForm.keyWord]:this.searchForm.keyWord,"dateRange":JSON.stringify(this.dateRange.map(date => date.format('YYYY-MM-DD HH:mm:ss')))}).then(
        res => {
          // 创建 Blob 对象
          const blob = new Blob([res], {
              type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          });

          // 创建下载链接
          const downloadUrl = window.URL.createObjectURL(blob);

          // 创建并触发隐藏的 <a> 标签下载文件
          const link = document.createElement('a');
          link.href = downloadUrl;
          link.download = 'Ozon订单数据.xlsx'; // 设置文件名
          document.body.appendChild(link);
          link.click();

          // 清理 DOM 和 URL
          document.body.removeChild(link);
          window.URL.revokeObjectURL(downloadUrl);
        }
      )
    }
  },
  mounted() {
    this.initialize();
    // 初始化动态列
    this.dynamicColumns = this.columns.map((col) => ({ ...col, visible: true }));
  },
  activated() {
    this.initialize();
  },
};
</script>

<style>
.drag-handle {
  cursor: grab;
}
.drag-handle:active {
  cursor: grabbing;
}
</style>
